/*
pour chaque element du tableau reu en argument de la fonction
    determiner le nombre obtenu par concatnation des proprits suivante:
        Salle+Rco+Ferme+Level
    injecter ce nombre dans une nouvelle proprit appele position
trier les elements du tableau par ordre croissant en se rfrant a la proprit position
*/

export default function sorter(tab) {
    const newTab =[]
    tab.forEach(e => {
        const rectifyFerme = e.ferme<10?"0"+e.ferme:e.ferme
        newTab.push({...e, position: parseInt(""+e.salle+e.rco+rectifyFerme+e.level)}) 

    })
    newTab.sort((a, b)=> { return a.position - b.position })
    return newTab
}